<template>
  <form @submit.prevent="handleSubmit">
    <div class="wrapper">
      <div class="title">
        Паспортные данные 
      </div>
      <div class="form">
        <div class="inputfield">
          <label>Тип документа*</label>
          <div class="custom_select">
            <select>
              <option value="pass">Паспорт</option>
              <option value="brthPass">Свидетельство о рождении</option>
              <option value="autoPass">Вод.Удостоверение</option>
            </select>
          </div>
        </div> 
        <div class="inputfield">
              <label>Серия</label>
              <input 
                type="number" 
                class="input" 
                name="serPass" 
                placeholder="****">
          </div>
          <div class="inputfield">
              <label>Номер</label>
              <input 
                type="number" 
                class="input" 
                name="numPass" 
                placeholder="******">
          </div>  
          <div class="inputfield">
              <label>Кем выдан</label>
              <input 
                type="text" 
                class="input" 
                name="plcPass" 
                placeholder="Отделом УФМС...">
          </div>  
          <div>
            <div class="inputfield">
                <label>Дата выдачи*</label>
                <input 
                  type="date" 
                  class="input" 
                  v-model.trim="fields.docDate" 
                  name="docDate"  
                  :class="{ 'is-invalid': submitted && $v.fields.docDate.$error }">
            </div>  
            <div 
              v-if="submitted && !$v.fields.docDate.required" 
              class="invalid-div">
                Поле должно быть заполнено
            </div> 
          </div>  
        <div class="inputfield">
          <input 
            type="submit" 
            value="Отправить заявку" 
            class="btn">
        </div>
      </div>
  </div>
  </form>
</template>

<script>
import { required } from "vuelidate/lib/validators";
export default {
  name: 'passForm',
  data() {
    return {
      fields: {
        docDate: ""
      },
      submitted: false
    }
  },
  validations: {
    fields: {
      docDate: { required }
    }
  },
  methods: {
    handleSubmit() {
      this.submitted = true
      if (this.$v.$invalid) {
        this.$v.$touch()
        return console.log('err from pass')
      }
      return alert('success' + JSON.stringify(this.fields))
    }
  }
}
</script>




