<template>
  <form @submit.prevent="handleSubmit">
    <div class="wrapper">
      <div class="title">
        Данные о местоположении
      </div>
      <div class="form">
        <div class="inputfield">
              <label>Индекс</label>
              <input 
                type="number" 
                class="input" 
                name="index" 
                placeholder="******">
          </div>
          <div class="inputfield">
              <label>Страна</label>
              <input 
                type="country" 
                class="input" 
                name="country" 
                placeholder="Россия">
          </div>  
          <div class="inputfield">
              <label>Область</label>
              <input 
                type="text" 
                class="input" 
                name="region" 
                placeholder="Московская">
          </div>  
          <div>
            <div class="inputfield">
                <label>Город*</label>
                <input 
                  type="text" 
                  class="input" 
                  v-model.trim="fields.city" 
                  name="city" placeholder="Москва" 
                  :class="{ 'is-invalid': submitted && $v.fields.city.$error }">
            </div>  
            <div 
              v-if="submitted && !$v.fields.city.required" 
              class="invalid-div">Поле должно быть заполнено
            </div> 
          </div>
          <div class="inputfield">
              <label>Улица</label>
              <input 
                type="text" 
                class="input" 
                name="street" 
                v-model = "fields.street">
          </div>  
          <div class="inputfield">
              <label>Дом</label>
              <input 
                type="number" 
                class="input" 
                name="house" 
                placeholder="№">
          </div>  
          <div class="inputfield terms">
            <label class="check">
              <input type="checkbox">
              <span class="checkmark"></span>
            </label>
            <p>Не отправлять СМС</p>
        </div> 
        <div class="inputfield">
          <input 
            type="submit" 
            value="Далее" 
            class="btn">
        </div>
      </div>
  </div>
  </form>
</template>

<script>
import { required } from "vuelidate/lib/validators";
export default {
  name: 'addressForm',
  data() {
    return {
      fields: {
        city: "",
        street: "ул."
      },
      submitted: false
    }
  },
  validations: {
    fields: {
      city: { required }
    }
  },
  methods: {
    handleSubmit() {
      this.submitted = true
      if (this.$v.$invalid) {
        this.$v.$touch()
        return console.log('err from address')
      }
      return alert('success')
    }
  }
}
</script>




