<template>
    <form @submit.prevent="handleSubmit">
      <div class="wrapper">
        <div class="title">
          Форма создания клиента
        </div>
        <div class="form">
          <div>
            <div class="inputfield">
              <label>Имя*</label>
              <input 
                type="text" 
                class="input" 
                v-model.trim="user.firstName" 
                placeholder="Иванов" 
                name="firstName" 
                :class="{ 'is-invalid': submitted && $v.user.firstName.$error }">
          </div>  
          <div 
            v-if="submitted && !$v.user.firstName.required" 
            class="invalid-div">
              Необходимо указать <strong>Имя</strong>
            </div>  
          </div>
          <div>
          <div class="inputfield">
            <label>Фамилия*</label>
            <input 
              type="text" 
              class="input" 
              v-model.trim="user.lastName" 
              placeholder="Иван" 
              name="lastName" 
              :class="{ 'is-invalid': submitted && $v.user.lastName.$error }">
          </div>
          <div 
            v-if="submitted && !$v.user.lastName.required" 
            class="invalid-div">
              Необходимо указать <strong>Фамилию</strong>
          </div>
        </div>
        <div>
          <div class="inputfield">
            <label>Отчество*</label>
            <input 
              type="text" 
              class="input" 
              v-model.trim="user.thirdName" 
              placeholder="Иванович" 
              name="thirdName" 
              :class="{ 'is-invalid': submitted && $v.user.thirdName.$error }">
          </div>
          <div 
            v-if="submitted && !$v.user.thirdName.required" 
            class="invalid-div">
              Необходимо указать <strong>Отчество</strong>
            </div>
        </div>  
        <div>
          <div class="inputfield">
              <label>Дата рождения*</label>
              <input 
                type="date" 
                class="input" 
                v-model="user.birthDate" 
                name="birthDate" 
                :class="{ 'is-invalid': submitted && $v.user.birthDate.$error }">
          </div>
          <div 
            v-if="submitted && !$v.user.birthDate.required" 
            class="invalid-div">
              Необходимо указать <strong>Дату Рождения</strong>
          </div>
        </div> 
        <div>
          <div class="inputfield">
              <label>Телефон*</label>
              <input 
                type="tel" 
                class="input" 
                v-model.trim="user.phone" 
                name="phone" 
                placeholder="7-(xxx)-xxxx-xxx" 
                maxlength="11" 
                :class="{ 'is-invalid': submitted && $v.user.phone.$error }">
          </div> 
          <div 
            v-if="submitted && !$v.user.phone.required" 
            class="invalid-div">
            Необходимо указать номер <strong>Телефона</strong>
          </div>
          <div 
            v-if="submitted && !$v.user.phone.minLength" 
            class="invalid-div">
              Номер должен состоять из <strong>11</strong> цифр
          </div>
        </div>
          <div class="inputfield">
            <label>Пол</label>
            <div class="custom_select">
              <select>
                <option value="">Выбрать</option>
                <option value="male">Муж.</option>
                <option value="female">Жен.</option>
              </select>
            </div>
        </div> 
        <div>
          <div class="inputfield">
              <label>Группа клиентов*</label>
              <div class="custom_select">
                <select>
                  <option value="nessGroup">ОМС</option>
                  <option value="problemGroup">Проблемные</option>
                  <option value="vip">VIP</option>
                </select>
              </div>
          </div> 
        </div>
        <div class="inputfield">
            <label>Лечащий врач</label>
            <div class="custom_select">
              <select>
                <option value="Иванов">Иванов</option>
                <option value="Захаров">Захаров</option>
                <option value="Чернышева">Чернышева</option>
              </select>
            </div>
        </div> 
        <div class="inputfield terms">
            <label class="check">
              <input type="checkbox">
              <span class="checkmark"></span>
            </label>
            <p>Не отправлять СМС</p>
        </div> 
          <div class="inputfield">
            <input type="submit" value="Далее" class="btn">
          </div>
        </div>
      </div>
    </form>
</template>


<script>
import { required, minLength } from "vuelidate/lib/validators"
export default {
  name: "user",
  data() {
    return {
      user: {
        firstName: "",
        lastName: "",
        thirdName: "",
        birthDate: "",
        phone: "",
      },
      submitted: false
    }
  },
  validations: {
    user: {
      firstName: { required },
      lastName: { required },
      thirdName: { required },
      birthDate: { required },
      phone: { required, minLength: minLength(11) },
    }
  },
  methods: {
    handleSubmit() {
      this.submitted = true
      if (this.$v.$invalid) {
        this.$v.$touch()
        return console.log('err from test')
      }
      return alert('success' + JSON.stringify(this.user))
    }
  }
}
</script>


